const Label = styled.label({
  color: theme.colors.grey_400,
})

const copyToClipboard = str => {
  const el = document.createElement('textarea')
  el.value = str
  document.body.appendChild(el)
  el.select()
  document.execCommand('copy')
  document.body.removeChild(el)
}

const formatCode = (value, type) => {
  const code = `(function() {
    var data = ${value};
    var script = document.createElement('script');
    script.type = 'application/ld+json';
    script.innerHTML = JSON.stringify(data);
    document.head.appendChild(script);
  })();`

  switch (type) {
    default:
    case 'prettified':
      const formattedCode = prettier.format(code, {
        parser: 'babel',
        plugins: [parserBabylon],
      })
      return `<script>\n${formattedCode}</script>\n<!--Google Tag Manager Friendly JSON-LD Code Generated by https://saijogeorge.com/json-ld-schema-generator/tag-manager-fix/-->`
    case 'minified':
      const { code: minifiedCode } = UglifyJS.minify(code)
      return `<script>${minifiedCode}</script><!--Google Tag Manager Friendly JSON-LD Code Generated by https://saijogeorge.com/json-ld-schema-generator/tag-manager-fix/-->`
  }
}

const stripScriptTags = value =>
  value.replace(/<\/?script[^>]*(>|$)(<!--.*)?/gim, '')

const Form = ({ codeFromOtherGenerator }) => {
  const [input, setInput] = useState(codeFromOtherGenerator || '')
  const [output, setOutput] = useState('')
  const [outputType, setOutputType] = useState('minified')
  const [error, setError] = useState(false)
  const formattedOutput = output && !error ? formatCode(output, outputType) : ''

  useEffect(() => {
    setError(false)

    if (input === '') {
      setOutput('')
      return
    }

    if (!input) {
      setError(true)
      setOutput('')
      return
    }

    const jsonInput = stripScriptTags(input)

    try {
      JSON.parse(jsonInput)
    } catch {
      setError(true)
      setOutput('')
      return
    }

    setOutput(jsonInput)
  }, [input])

  return (
    <div
      css={{
        display: 'grid',
        gridTemplateColumns: '1fr',
        gridGap: rem(20),
        padding: rem(20),
        [media.tabletLg]: {
          gridTemplateColumns: '1fr 1fr',
          gridGap: rem(30),
          padding: rem(30),
        },
        [min(1200)]: {
          gridGap: rem(40),
          padding: rem(40),
        },
        background: '#fff',
        borderRadius: 8,
        boxShadow: '0 5px 30px #CBD6D9',
      }}
    >
      <div>
        <Label htmlFor="input-code">JSON-LD input</Label>
        <Space y={10} />
        <TextArea
          id="input-code"
          autosize={{ minRows: 8 }}
          value={input}
          placeholder="Paste code here"
          onChange={e => setInput(e.target.value)}
          css={{
            maxHeight: 'calc(100vh - 150px) !important',
            fontFamily: 'monospace',
            resize: 'none',
            '&.ant-input': { height: 178 },
          }}
        />
        <Space y={20} />
        {error && (
          <Fragment>
            <Alert
              message={
                <form
                  method="post"
                  target="_blank"
                  action="https://search.google.com/structured-data/testing-tool"
                >
                  <p css={{ display: 'inline' }}>Please enter valid code.</p>
                  <Space inline x={10} />
                  <Button type="ghost" size="small" htmlType="submit">
                    Test on Google
                  </Button>
                  <textarea
                    name="code"
                    value={input}
                    readOnly
                    css={{ display: 'none' }}
                  />
                </form>
              }
              type="error"
            />
            <Space y={20} />
          </Fragment>
        )}
        {/* <Button
          type="primary"
          size="default"
          onClick={() => {
            setError(false)

            if (!input) setError(true)

            const jsonInput = stripScriptTags(input)

            try {
              JSON.parse(jsonInput)
            } catch {
              setError(true)
              return
            }

            setOutput(jsonInput)
            navigate('tag-manager-fix/#output')
          }}
        >
          Generate Tag Manager Code
        </Button> */}
      </div>
      <div id="output" css={{ marginTop: rem(-14) }}>
        <Label
          htmlFor="output-code"
          css={{
            display: 'inline-block',
            paddingTop: rem(14),
          }}
        >
          Google Tag Manager Friendly Code
        </Label>
        <Space y={10} />
        <TextArea
          id="output-code"
          autosize={{ minRows: 8 }}
          value={formattedOutput}
          disabled={!output}
          readOnly
          css={{
            maxHeight: 'calc(100vh - 150px) !important',
            fontFamily: 'monospace',
            resize: 'none',
            '&.ant-input': { height: 178 },
          }}
        />
        <Space y={20} />
        <div
          css={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginTop: -20,
            marginLeft: -20,
            '> *': {
              marginTop: 20,
              marginLeft: 20,
            },
          }}
        >
          <Button
            type="primary"
            size="default"
            disabled={!output}
            onClick={() => {
              copyToClipboard(formattedOutput)
              message.success('Copied to clipboard!')
            }}
          >
            Copy Tag Manager Code
          </Button>
          <div css={{ color: theme.colors.grey_200 }}>
            <Radio.Group
              value={outputType}
              onChange={e => setOutputType(e.target.value)}
              size="large"
              disabled={!output}
            >
              <Radio value="minified">Minified</Radio>
              <Radio value="prettified">Prettified</Radio>
            </Radio.Group>
            <a
              href="#should-i-choose-prettified-or-minified-version"
              title="Should I choose Prettified OR Minified version?"
            >
              (What is this?)
            </a>
          </div>
        </div>
      </div>
    </div>
  )
}

